*{
    margin:0;
    padding: 0;
  }
  
  :root{
    /* Spacing */
   /* 1rem = 16px */
   --space-0: 0;
   --space-1: 0.25rem;
   --space-2: 0.5rem;
   --space-3: 0.75rem;
   --space-4: 1rem;
   --space-5: 1.25rem;
   --space-6: 1.5rem;
   --space-8: 2rem;
   --space-10: 2.5rem;
   --space-12: 3rem;
   --space-16: 4rem;
   --space-20: 5rem;
   --space-24: 6rem;
  
   /* Font Size */
  
   --text-sm: 0.875rem;
   --text-md: 1rem;
   --text-lg: 1.25;
   --text-xlg: 1.5rem;
  
   /* Border radius */
   --radius: 6px;
   --round: 1000px;
  
   /* Border */
   --border: 1px solid var(--color-border);
  
   /* Shadow */
   --shadow: 0 2px 8px rgba(0,0,0,0.06), 0 2px 3px rgba(0,0,0,0.05);
   --shadow-focus: 0 0 0 4px rgba(140,26,26,0.9);
   --shadow-large: 0px 5px 18px rgba(0, 0, 0, 0.1),
                     0px 1px 3px rgba(0, 0, 0, 0.05);
  
   /* Transition */
   --transition: all 1s var(--transition-curve);
   --transition-bounce: all var(--transition-speed) var(--transition-curve-bounce);
  
   /* Transitions curve*/
   --transition-curve: cubic-bezier(0.2,0.7,0.3,1);
   --transition-curve-bounce: cubic-bezier(0.175, 0.885, 0.32, 1.275);
  
   /* Transition speed */
   --transition-speed: .25s;
   --transition-speed-slow: 1s;
  
   /* Opacities */
   --opacity-25: 0.25;
   --opacity-50: 0.50;
   --opacity-75: 0.75;
   --opacity-100: 1.00;
  
  }
  
  body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
  
  .App{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100vh;
    
  }
  
  #preview{
    display: inline-block;
    width: 50%;
    //padding: var(--space-2) var(--space-12) 0 var(--space-4);
    overflow: scroll;
    background-color: #FCECEC;
  
  }

  #preview > *{
      
      margin: 1rem 0;
      margin-left: var(--space-6);
      
  }
  
  #preview{
    //blockquotes
    blockquote{
        border-left: 3px solid black;
        margin-left: var(--space-10);
        padding-left: var(--space-2);
    }

    //ordered list
    //unordered lsit
    li{
        margin-left: var(--space-4)
    }
    //code
    code{
        background-color: white;
        
    }

    pre{
        display: inline-block;
        background-color: white;
    }
    //horizontal rule
    //link
    //image
    //table
    table, th, td {
        border: 1px solid black;
    }
    th, td{
        padding: var(--space-2) var(--space-1);
    }
   
    //footnote
    //definition list
    //task list
      
  }
  #editor{
    width: 50%;
    padding: var(--space-2) var(--space-8) 0 var(--space-4);
    outline: none;
    border: none;
    resize: none;
    font-size: var(--text-lg);
  }
  
